.mrm-email-editor {
    .mrm-editor-header {
      box-shadow: 0px 1px 1px #b6b9bf4d;
      display: flex;
      align-items: center;
      flex-flow: row wrap;
      justify-content: space-between;
      padding: 18px 30px;
  
      .header-left {
        display: flex;
        align-items: center;
      }
  
      .header-right {
        display: flex;
        align-items: center;
      }
  
      .arco-icon-hover.arco-page-header-icon-hover::before {
        display: none;
      }
  
      .arco-page-header-head-extra {
        white-space: normal;
        overflow: visible;
        display: flex;
        align-items: center;
      }
  
      .arco-btn {
        border: 1px solid #e4e6eb;
        border-radius: 6px;
        font-weight: 500;
        font-size: 15px;
        line-height: 1.2;
        letter-spacing: -0.01em;
        color: #2d3149;
        padding: 10px 16px;
        height: auto;
        text-transform: capitalize;
        background-color: transparent;
        margin-left: 14px;
        outline: none;
        box-shadow: none;
        transition: none;
        transition: all 0.3s ease;
  
        &:first-child {
          margin-left: 0;
        }
  
        &:hover {
          background-color: #f6f8fa !important;
          border-color: #e4e6eb !important;
          color: #2d3149 !important;
        }
  
        &.next-btn {
          background: $primary-color;
          border-color: #e4e6eb;
          color: #fff;
        }
        &.next-btn:hover {
          background-color: #4c25a5 !important;
          color: #fff !important;
        }
        .mintmrm-loader {
          display: inline-block;
          position: relative;
          top: 2px;
        }
        &:disabled {
          opacity: 0.7;
          background: #eaeef1;
          cursor: not-allowed;
          color: #c0c1cd;
        }
      }
  
      .back-from-editor {
        width: 40px;
        height: 40px;
        padding: 10px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
      }
  
      .more-option {
        padding: 9px 9px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
  
        .more-option-list {
          list-style: none;
          margin: 0;
          position: absolute;
          left: 0;
          top: calc(100% + 10px);
          z-index: 99;
          background: #ffffff;
          border: 1px solid #f0f1f4;
          box-shadow: 0px 6px 8px #bfbfbf1a;
          border-radius: 6px;
          padding: 8px;
          width: 163px;
          display: none;
  
          li {
            button {
              display: block;
              width: 100%;
              margin: 0;
              font-weight: 500;
              font-size: 14px;
              line-height: 1.14;
              color: #2d3149;
              padding: 8px 8px;
              border: none;
              text-align: left;
              border-radius: 4px;
              margin-bottom: 1px;
  
              &:hover {
                background: #f6f8fa !important;
              }
            }
            .has-sub-dropdown {
              &::before {
                content: url(data:image/svg+xml,%3Csvg%20fill%3D%22none%22%20viewBox%3D%220%200%206%2010%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20stroke%3D%22%23A7A8B3%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22%20stroke-width%3D%221.5%22%20d%3D%22M1%201l4%204-4%204%22%2F%3E%3C%2Fsvg%3E);
                position: absolute;
                width: 6px;
                top: 50%;
                right: 10px;
                transform: translateY(-50%);
                transition: all 0.3s ease;
              }
              &.show {
                &::before {
                  top: 25%;
                  transform: rotate(90deg);
                  transition: all 0.3s ease;
                }
              }
            }
            .group-item {
              transition: all 0.3s ease;
              padding-left: 20px;
            }
          }
        }
        .save-template-btn{
          position: absolute;
          left: 0;
          top: calc(100% + 10px);
          width: 240px;
          z-index: 99;
          padding: 20px;
          background: #fff;
          border: 1px solid #f0f1f4;
          border-radius: 6px;
          box-shadow: 0 6px 8px hsl(0deg 0% 75% / 10%);
          text-align: left;
          display: none;
          flex-flow: column wrap;
          align-items: flex-start;
          gap: 10px;
  
          label{
            font-size: 14px;
          }
  
          input[type="text"]{
            padding: 9px 10px;
            font-weight: 300;
            font-size: 13px;
            border: 1px solid #ebedf0;
            line-height: 1.3;
            width: 100%;
            box-shadow: none;
            outline: none;
          }
  
          .arco-btn {
            margin-left: 0;
            background: #573BFF;
            color: #fff;
            padding: 10px 18px;

            &:hover {
              background-color: #4c25a5 !important;
              color: #fff !important;
            }
          }
          &.active{
            display: flex;
          }
          
  
        }
  
        &.show-option-list {
          .more-option-list {
            display: block;
          }
        }
      }
  
      .mood-change-btn {
        padding: 10px 10px;
        width: 40px;
      }
  
      .responsive-check {
        border: 1px solid #e4e6eb;
        border-radius: 6px;
        margin-left: 14px;
        display: flex;
  
        button {
          height: 40px;
          padding: 6px;
          width: 40px;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          margin: 0;
          border: none;
          border-right: 1px solid #e4e6eb;
          border-radius: 0;
  
          &:last-child {
            border-right: 0;
            border-radius: 0 6px 6px 0;
          }
          &:first-child {
            border-radius: 6px 0 0 6px;
          }
  
          &:hover {
            background: #f1f1f1 !important;
          }
  
          &.active {
            background-color: #f6f8fa !important;
          }
        }
      }
    }
  }
  