.email-builder-editor {
  .mintmrm-delete-alert-wrapper {
    padding: 0;
    display: none;

    &.show-modal {
      display: flex;
    }
  }
}

.arco-trigger {
  z-index: 9999 !important;
}

.email-builder-loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 101;
  background: #00000054;
  display: none;

  &.show-loader {
    display: block;
  }

  .mintmrm-loader {
    border: 2px solid #222222;
    border-top-color: #fff;
    width: 50px;
    height: 50px;
    margin-left: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    transform: translate(-50%, -50%);
  }
}

.mrm-email-editor {
  & > .arco-card {
    // margin: 30px;
    // width: calc(100% - 60px);
    // border: none;
    background: transparent;

    border: none;
    // background: #c21212bf;
    height: calc(100vh - 110px);
    padding: 30px!important;
    width: 100%;

    .easy-email-overlay{
      & > div{
        & > div{
          background-image: none !important;
          box-shadow: 0px 20px 30px rgb(217 220 223 / 40%);
          border-radius: 20px;
          background: #fff;
          z-index: 0 !important;
        }
      }
    }
  }

  .arco-collapse-item-content-box {
    padding: 8px 20px 8px 20px;
  }

  hr {
    border: none;
    border-bottom: 1px solid #ddd;
  }

  .mintmrm-delete-alert-wrapper .mintmrm-delete-confirmation {
    max-width: 530px;

    .delete-confirmation-header {
      background-color: transparent;
      border-bottom-color: #ebedf0;
      position: relative;
      padding: 25px 30px 25px 30px;

      .cross-icon {
        height: 46px;
        width: 46px;
        box-shadow: 0px 1px 1px #dadada;
        border-radius: 100px;
        border: none;
        position: absolute;
        right: -14px;
        top: -15px;
      }
    }

    .delete-confirmation-body {
      display: block;
      padding: 5px 30px 30px;

      .form-group {
        label {
          margin-bottom: 7px;
          color: #2d3149;
          font-weight: 600;
        }
        input[type="email"] {
          font-size: 14px;
          min-height: 48px;
          color: #6a707c;
          border-color: #E9EAEE;
        }
      }
    }

    .mintmrm-delete-confirm-btn {
      li {
        margin: 0;
      }

      .cancel {
        margin-right: 14px;
      }

      .alert-message {
        p {
          color: #09af09;
          margin-right: 10px;
          font-size: 14px;
          font-weight: 500;
        }

        &.mintmrm-error p {
          color: #eb1010;
        }
      }

      .mintmrm-loader {
        display: inline-block;
        position: relative;
        top: 2px;
      }
    }
  }

  ._StandardLayout_xv0bc_1 .arco-layout-sider-children .arco-tabs-content {
    border-radius: 0 0 10px 10px;
  }

  & > .arco-card > .arco-card-body {
    height: calc(100vh - 170px) !important;
  }

  .arco-card-body {
    & > .arco-layout {
      width: 100% !important;
      height: calc(100vh - 170px) !important;

      & > .arco-layout {
        background: #ffffff;
        border-radius: 10px;
        padding: 3px;
        width: calc(100% - 430px);
        flex: calc(100% - 430px) 0 0 !important;
        margin-right: 30px;
        height: calc(100vh - 170px) !important;

        .shadow-container {
          padding: 0 30px;
        }
      }

      .os-viewport-scrollbar-hidden {
        & > div {
          height: calc(100vh - 230px) !important;
        }
      }
    }
  }
}
