    //-------text align control------
    .mrm-stylish-radio {
      .arco-radio-group {
        border: 1px solid #E4E6EB;
        border-radius: 6px;

        .arco-radio {
          padding: 9px 10px;
          margin: 0;
          display: block;
          border-right: 1px solid #E4E6EB;

