            flex: 1;
            text-align: center;
            margin: 0;

            &:first-child {
              .arco-radio {
                border-radius: 6px 0 0 6px;
              }
            }

            &:last-child {
              .arco-radio {
                border-right: 0;
                border-radius: 0 6px 6px 0;
              }
            }
          }
        }
      }
    }

