    //----theme style pannel design------
    .mrm-theme-settings-pannel {
      padding: 16px;

      .mrm-theme-settings-wrapper {
        background: #f6f8fa;
        padding: 10px 10px 0px;
        border-radius: 8px;
      }

      .label-block {
        .arco-row {
          .arco-col:first-child {
            width: 100%;
            flex: 0 0 100%;
            padding-right: 0;
            margin-bottom: 8px;
          }

          .arco-col:nth-child(2) {
            width: 100%;
            flex: 100%;
            margin: 0;
          }

          .arco-textarea {
            min-width: 100%;
            height: auto!important;
            min-height: 100px;
          }
        }
      }

      .mrm-tab-title {
        margin-bottom: 8px;

        h4 {
          font-size: 15px;
          color: #000;
          font-weight: 600;
        }
      }
    }

    .mrm-padding-wrapper {
      display: flex;
      width: 100%;

      & > .arco-space {
        width: calc(100% / 4 - 10px)!important;
        margin-left: 10px;

        &:first-child {
          margin-left: 0;
        }

        .arco-row {
          display: block;

          .arco-col:first-child {
            width: auto;
            flex: 0 0 auto;
            padding-right: 0;
            margin-bottom: 10px;
          }

          .arco-col:nth-child(2) {
            width: 100%;
            flex: 0 0 100%;
          }
        }
      }
    }

    .page-block-item .arco-collapse-item-content-box > ._Stack_1jdgv_1,
    .wrapper-block-border-item .arco-collapse-item-content-box > ._Stack_1jdgv_1,
    .wrapper-block-bg-wrapper .arco-collapse-item-content-box > ._Stack_1jdgv_1,
    .image-bg-wrapper .arco-collapse-item-content-box > ._Stack_1jdgv_1 {
      background: #F6F8FA;
      border-radius: 8px;
      padding: 20px 20px 5px;
    }

    .image-uploader {
      ._upload_jzksr_68 {
        border-radius: 7px;
      }

      .img-upload-url {
        box-shadow: 0px 1px 1px #b6b9bf4d;
        margin-top: 8px;

        .arco-btn-size-default.arco-btn-icon-only {
          background: #fff;
          border-right: 1px solid rgb(225 225 225);
        }
        .arco-input {
          background: #fff;
        }
      }
    }

    .image-bg-wrapper {
      ._Item_1jdgv_8 hr {
        margin: 12px 0;
      }
    }

    .mrm-button-setting-wrapper {
      margin-bottom: 10px;
    }

    .mrm-mode-setting .arco-row .arco-col:first-child {
      width: 110px;
      flex: 0 0 110px;
    }
    .mrm-mode-setting .arco-row .arco-col:nth-child(2) {
      width: calc(100% - 110px);
      flex: 0 0 calc(100% - 110px);
    }

    .divider-border-item .arco-collapse-item-content-box > ._Stack_1jdgv_1 {
      margin: 0;
      background: #F6F8FA;
      border-radius: 8px;
      padding: 20px 20px 5px;
    }
    .divider-border-item .arco-collapse-item-content-box > ._spacingTight_1jdgv_38 > ._Item_1jdgv_8 {
      margin: 0;
    }


    .social-item-wrapper {
      .mrm-inline-label .arco-input {
        background: #f6f8fa;
      }
      .mrm-inline-label .arco-input-group > .arco-input-inner-wrapper-has-prefix {
        background: #f6f8fa;

        .arco-input {
          background: transparent;
        }
      }

      .arco-card.arco-card-size-default.arco-card-contain-grid {
        background: transparent;

        & > .arco-card-body {
          & > .arco-card-grid {
            border-radius: 10px;
            margin-bottom: 10px;
            background: #fff;
            box-shadow: 0px 1px 1px #b6b9bf4d;

            .arco-card-bordered {
              border: none;
            }
            .arco-card-size-default .arco-card-header {
              border-bottom: 1px solid #ebedf0;
            }

            .arco-card-bordered {
              .arco-card-body {
                & > .arco-space {
                  & > .arco-space-item:first-child {
                    margin-bottom: 15px!important;
                  }
                  & > .arco-space-item:nth-child(2) {
                    margin-bottom: 16px!important;
                  }
                }
              }
            }
          }
        }
      }
    }

    .mrm-layout-wrapper {
      padding: 16px;

      .mrm-single-layout {
        width: 100%;
        background: #f6f8fa;
        border-radius: 8px;
        margin-bottom: 16px;
        padding: 10px;

        .layout-box-wrapper {
          margin-bottom: 0!important;

          &:not(:first-child) {
            margin-top: 10px;
          }

          &[style*="height: 0px"] {
            margin-top: 0;
            margin-bottom: 0px!important;
          }
        }

        .layout-box {
          border: none!important;
          padding: 10px 62px 10px 10px!important;
          background: #FFFFFF;
          box-shadow: 0px 1px 1px #b6b9bf4d;
          border-radius: 6px;
          position: relative;
          cursor: all-scroll;

          &:before {
            content: url("data:image/svg+xml;base64,");
            position: absolute;
            right: 10px;
            width: 10px;
            top: 50%;
            transform: translateY(-50%);
            line-height: 1;
          }
        }

        .layout-inner {
          border-color: #A7A8B3!important;

          & > div {
            border-color: #A7A8B3!important;
          }
        }
      }
    }


  }//---end aside.arco-layout-sider----


